.header {
  min-height: 100vh;
  position: relative;
  z-index: 1;
}

.header__inner {
  padding: 30px 0;
  text-align: center;
}

.header__logo {
  display: block;
  margin: auto;
  width: 600px;
  height: 85px;
  margin-bottom: 160px;
}

.header__text {
  font-size: 142px;
  line-height: 140px;
  font-weight: bold;
  background: -webkit-linear-gradient(#ffe402, #ff0404);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  /*  font-size: 72px;
    background-image: -webkit-linear-gradient(#eee, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    position: relative; */
  /*&:after {      
       background: none;
      content: attr(data-text);
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      position: absolute;
      text-shadow: 5px 5px 5px red;

      z-index: -1; 
    }*/
}

.header__text:after {
  width: 100%;
  content: attr(data-text);
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8);
  top: 0;
  z-index: -1;
}

.header__date {
  font-size: 80px;
  color: #fff;
  line-height: 80px;
  padding: 20px;
}

.header__go {
  width: 300px;
  margin: auto;
}

.header__scroll {
  display: flex;
  justify-content: center;
  padding-top: 60px;
}

.header__scrollLink {
  display: flex;
  justify-content: center;
}

@media screen and (max-width: 768px) {
  .header__logo {
    width: 500px;
    margin-bottom: 60px;
  }
}

@media screen and (max-width: 640px) {
  .header__logo {
    width: 300px;
    margin-bottom: 30px;
  }

  .header__text {
    font-size: 80px;
    line-height: 80px;
  }

  .header__date {
    font-size: 40px;
  }

  .header__go {
    padding-top: 100px;
  }

  .header__scroll {
    padding-top: 40px;
  }

  .header__scrollImg {
    width: 70%;
  }
}

@media screen and (max-width: 540px) {
  .header__logo {
    font-size: 60px;
    margin-bottom: 10px;
  }

  .header__text {
    font-size: 60px;
    line-height: 60px;
  }

  .header__go {
    padding-top: 15px;
    width: 200px;
  }
}